@import './fonts-and-colors.scss';

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Ubuntu', sans-serif;
    font-weight: 300;
}

h1 {
    font-weight: 700;
    color: $primary-font-color;
    font-size: 3rem;
}

h2 {
    font-weight: 400;
    color: $primary-font-color;
}

h3 {
    font-weight: 400;
    color: $primary-font-color;
}

p {
    font-size: 1.15rem;
    color: $secondary-font-color;
}

.link-set {
    .icon {
        transition: all 150ms ease-in-out;

        &:hover {
            scale: 1.25;
        }
    }
}

button {
    border: 1px solid black;
    padding: 0.5rem 1rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 1rem;
    font-weight: 400;
    cursor: pointer;
    transition: all 150ms ease-in-out;
    background-color: $button-bg-color;
    color: $button-font-color;

    &:hover {
        background-color: $button-hover-bg-color;
        color: $button-hover-font-color;
    }
}

header {
    display: flex;
    position: fixed;
    z-index: 1000;
    width: 100%;
    align-items: center;
    flex-direction: column;
}

@import './kezdolap.css';
@import './navigation.css';
@import './magamrol.css';
@import './arak.css';
@import './gyik.css';

.test-content {
    min-height: 700px;
    padding-top: 200px;
    padding-left: 50px;
}